#sidebar-left {
  display: none; }

@media screen and (min-width: 700px) {
  #sidebar-left {
    position: fixed;
    display: block;
    grid-area: leftnav;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.82);
    z-index: 10; }
  #sidebar-left h3 {
    text-align: center;
    color: #7f818f; }
  #sidebar-left nav ul li {
    padding: 10px 10px 0 10px; }
  #sidebar-left nav ul li a {
    display: inline-block;
    width: 100%;
    color: #b6b6b6;
    text-decoration: none;
    padding: 5px;
    border-radius: 10px; }
  #sidebar-left nav ul li a span {
    display: none; }
  #sidebar-left nav ul li a.left-nav-active {
    color: white;
    background: #3d3d3d; }
  #sidebar-left nav ul li a:hover {
    color: white; }
  .sidenav-sign-out {
    display: flex;
    position: absolute;
    bottom: 20px;
    width: 100%;
    color: white;
    justify-content: center;
    cursor: pointer; }
  .sidenav-sign-out div {
    display: none; }
  .sidenav-sign-out span.sign-out-icon {
    display: none; }
  .sidenav-sign-out span.power-out-icon {
    display: inline; } }

@media screen and (min-width: 1000px) {
  #sidebar-left {
    width: 150px; }
  #sidebar-left nav ul li a span {
    display: inline;
    margin-left: 10px; }
  #sidebar-left nav ul li a {
    padding: 5px 10px; }
  .sidenav-sign-out div {
    display: block; }
  .sidenav-sign-out span.sign-out-icon {
    display: inline;
    margin-left: 15px; }
  .sidenav-sign-out span.power-out-icon {
    display: none; } }

.tasks-date-heading {
  display: flex;
  justify-content: space-between;
  padding: 10px;
  font-size: .9em;
  text-transform: capitalize;
  color: #3d3d3d; }

.tasks-rows {
  width: 100%;
  list-style: none; }

.timer-active-task-row,
.timer-task-row {
  clear: left;
  height: 50px;
  padding: 5px;
  border-bottom: 1px solid rgba(116, 118, 143, 0.1); }

.timer-active-task-row {
  height: 70px;
  margin-bottom: 30px; }

.timer-task-row {
  font-size: .9em; }

.timer-task-row-active {
  background-color: whitesmoke; }

.ttr-left, .ttr-right {
  height: 100%; }

.ttr-left {
  float: left;
  width: 60%; }

.ttr-right, .ttr-active-right {
  float: right;
  display: inline-flex;
  align-items: center;
  justify-content: space-between;
  height: 100%;
  width: 40%;
  max-width: 400px; }

.ttr-description-wrapper {
  float: left;
  max-width: 70%;
  height: 100%; }

.ttr-active-task-row.ttr-description-wrapper {
  min-width: 30%; }

.ttr-description {
  display: flex;
  align-items: center;
  height: 100%;
  color: #616161;
  margin-right: 20px; }

.ttr-description-input {
  width: 100%;
  height: 100%;
  color: #242424;
  background-color: white;
  padding: 0 10px;
  border: none; }

.ttr-description, .ttr-description-input {
  max-width: 500px;
  overflow: hidden; }

.ttr-description-input::-webkit-input-placeholder {
  color: #a7a7a7; }

.ttr-description-input::-moz-placeholder {
  color: #a7a7a7; }

.ttr-description-input:-ms-input-placeholder {
  color: #a7a7a7; }

.ttr-description-input::-ms-input-placeholder {
  color: #a7a7a7; }

.ttr-description-input::placeholder {
  color: #a7a7a7; }

.ttr-description-input-active {
  background-color: whitesmoke; }

.ttr-last {
  float: right;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 60%; }

.ttr-active-right .ttr-last {
  justify-content: flex-end;
  width: 40%;
  margin-left: 10px; }

.ttr-times {
  display: none;
  float: left;
  color: #a7a7a7; }

.ttr-start-button, .ttr-stop-button {
  float: left;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 35px;
  width: 35px;
  color: white;
  cursor: pointer;
  border-radius: 50px;
  margin-left: 15px; }

.ttr-start-button {
  background: #18b318; }

.ttr-stop-button {
  background: #e91010; }

.ttr-delete {
  cursor: pointer;
  color: #c4c4c4; }

.ttr-delete:hover {
  color: #a7a7a7; }

/* dropdown */
.ttr-dropdown {
  height: 100%; }

.ttr-project-select, .ttr-label-select, .ttr-no-selected {
  display: inline-flex;
  align-items: center;
  width: 50px;
  cursor: pointer; }

.ttr-project-select {
  width: 100%;
  margin-left: 20px; }

.ttr-project-select.ttr-dropdown-expanded,
.ttr-label-select.ttr-dropdown-expanded {
  background: whitesmoke; }

.ttr-project-select {
  max-width: 150px; }

.ttr-label-select {
  width: 100px; }

.ttr-dropdown-icon {
  color: #a7a7a7;
  font-size: .9em; }

.ttr-dropdown-project-dot {
  display: block;
  float: left;
  height: 6px;
  width: 6px;
  border-radius: 12px;
  margin-top: 4px;
  margin-right: 5px; }

.ttr-dropdown-list-wrapper {
  position: relative; }

.ttr-dropdown-list {
  position: absolute;
  top: 30px;
  left: -50px;
  z-index: 10;
  padding: 10px;
  background: white;
  width: 300px;
  max-height: 350px;
  overflow-y: scroll;
  overflow-x: hidden; }

.ttr-dropdown-list li {
  list-style: none;
  padding: 10px 5px; }

@media screen and (min-width: 1150px) {
  .ttr-times {
    display: block;
    float: left; } }

.listing-table {
  width: 100%;
  border-collapse: collapse; }

.listing-table thead {
  margin-bottom: 20px;
  border-bottom: thick solid whitesmoke; }

.listing-table th {
  text-align: left;
  padding: 10px;
  color: #7c7c7c; }

.listing-table td {
  height: 50px;
  font-size: .9em;
  color: #616161;
  padding-left: 15px;
  border-bottom: thin solid whitesmoke; }

.listing-project-color-wrapper {
  float: left;
  height: 50px;
  width: 30px;
  padding: 20px 0; }

.listing-project-color {
  display: block;
  height: 10px;
  width: 10px;
  border-radius: 50px; }

.listing-item-wrapper {
  line-height: 50px; }

.listing-td-actions {
  width: 10%; }

.listing-td-actions div {
  float: right;
  padding: 5px;
  color: #a7a7a7;
  font-weight: bold;
  cursor: pointer; }

.listing-td-actions div:hover {
  color: #3d3d3d; }

.popup-overlay {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  height: 100%;
  width: 100%;
  background: rgba(0, 0, 0, 0.3); }

.popup-hide {
  display: none; }

.popup-form {
  display: grid;
  grid-template-rows: 50px 100px 50px;
  height: 240px;
  width: 700px;
  margin-top: 150px;
  background: whitesmoke;
  padding: 10px 30px; }

.popup-delete {
  display: grid;
  grid-template-rows: 50px 100px;
  height: 160px;
  width: 400px;
  margin-top: 150px;
  background: whitesmoke;
  padding: 10px 30px; }

.popup-edit {
  display: grid;
  grid-template-rows: 50px 100px 50px;
  height: 240px;
  width: 400px;
  margin-top: 150px;
  background: whitesmoke;
  padding: 10px 30px; }

.popup-form-row-1 {
  display: flex;
  justify-content: space-between;
  align-items: center; }

.popup-form-row-2,
.popup-delete-row-2 {
  display: flex;
  justify-content: center;
  align-items: center; }

.popup-form-row-2 > div {
  width: 100%; }

.popup-form-row-2 > div > div {
  display: grid;
  grid-template-columns: auto 200px;
  margin-bottom: 10px; }

.popup-form-row-2 > div > span {
  padding: 10px;
  color: #eb5252; }

.popup-form-row-3 {
  display: flex;
  justify-content: space-between;
  align-items: flex-end; }

.popup-form-edit-row-3 {
  display: flex;
  justify-content: center;
  align-items: center; }

.popup-heading {
  color: #3d3d3d; }

.popup-close {
  color: #7c7c7c;
  cursor: pointer; }

.popup-close:hover {
  color: #3d3d3d; }

.popup-input {
  height: 50px;
  padding: 10px;
  border: none; }

.popup-selected-client-container {
  position: relative;
  display: grid;
  grid-template-columns: auto 30px;
  border-left: solid thin whitesmoke;
  color: #616161;
  cursor: pointer; }

.popup-selected-client,
.popup-selected-client-caret {
  display: flex;
  align-items: center;
  background: white; }

.popup-selected-client {
  padding: 0 10px;
  font-size: 12px;
  font-weight: bold;
  text-transform: uppercase; }

.popup-selected-client-caret {
  padding: 0 5px; }

.popup-client-dropdown {
  position: absolute;
  top: 50px;
  left: 0;
  width: 200px;
  min-height: 50px;
  background: white; }

.popup-client-dropdown ul {
  list-style: none;
  font-weight: 400; }

.popup-client-dropdown li {
  padding: 10px;
  cursor: pointer; }

.popup-client-dropdown li:hover {
  background: whitesmoke; }

.popup-client-dropdown li.selected {
  font-weight: bold; }

.popup-selected-color-container {
  position: relative;
  display: grid;
  grid-template-columns: 30px 25px;
  background: white;
  padding: 10px 2px 10px 10px;
  cursor: pointer; }

.popup-selected-color-container > div {
  display: flex;
  justify-content: center;
  align-items: center; }

.popup-selected-color-container-caret {
  color: #7c7c7c; }

.popup-selected-color-container-caret:hover {
  color: #3d3d3d; }

.popup-color-palette-container {
  position: absolute;
  top: 60px;
  left: 0; }

.popup-selected-color {
  height: 30px;
  width: 30px;
  border-radius: 3px; }

.button {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 45px;
  min-width: 200px;
  padding: 0 20px;
  font-size: 14px;
  font-weight: bold;
  color: white;
  cursor: pointer; }

.create-button {
  background: #55d841;
  box-shadow: inset 0 -2px 0 #3bbe27; }

.create-button:hover {
  background: #3bbe27; }

.delete-button {
  background: red;
  box-shadow: inset 0 -2px 0 #ba1212; }

.delete-button:hover {
  background: #ba1212; }

.button-disabled, .button-disabled:hover {
  background: #7c7c7c;
  cursor: default;
  box-shadow: none; }

.color-palette {
  display: grid;
  grid-template-columns: repeat(5, 25px);
  grid-gap: 7px;
  grid-auto-rows: 25px;
  background: white;
  padding: 15px; }

.color-swatch {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 3px;
  cursor: pointer;
  color: white; }

.lds-ellipsis {
  display: inline-block;
  position: relative;
  width: 64px;
  height: 30px; }

.lds-ellipsis div {
  position: absolute;
  top: 10px;
  width: 11px;
  height: 11px;
  border-radius: 50%;
  background: #bababa;
  -webkit-animation-timing-function: cubic-bezier(0, 1, 1, 0);
          animation-timing-function: cubic-bezier(0, 1, 1, 0); }

.lds-ellipsis div:nth-child(1) {
  left: 6px;
  -webkit-animation: lds-ellipsis1 0.6s infinite;
          animation: lds-ellipsis1 0.6s infinite; }

.lds-ellipsis div:nth-child(2) {
  left: 6px;
  -webkit-animation: lds-ellipsis2 0.6s infinite;
          animation: lds-ellipsis2 0.6s infinite; }

.lds-ellipsis div:nth-child(3) {
  left: 26px;
  -webkit-animation: lds-ellipsis2 0.6s infinite;
          animation: lds-ellipsis2 0.6s infinite; }

.lds-ellipsis div:nth-child(4) {
  left: 45px;
  -webkit-animation: lds-ellipsis3 0.6s infinite;
          animation: lds-ellipsis3 0.6s infinite; }

@-webkit-keyframes lds-ellipsis1 {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0); }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1); } }

@keyframes lds-ellipsis1 {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0); }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1); } }

@-webkit-keyframes lds-ellipsis3 {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1); }
  100% {
    -webkit-transform: scale(0);
            transform: scale(0); } }

@keyframes lds-ellipsis3 {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1); }
  100% {
    -webkit-transform: scale(0);
            transform: scale(0); } }

@-webkit-keyframes lds-ellipsis2 {
  0% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0); }
  100% {
    -webkit-transform: translate(19px, 0);
            transform: translate(19px, 0); } }

@keyframes lds-ellipsis2 {
  0% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0); }
  100% {
    -webkit-transform: translate(19px, 0);
            transform: translate(19px, 0); } }

.fade-appear {
  opacity: 0;
  z-index: 1; }

.fade-appear.fade-appear-active {
  opacity: 1;
  -webkit-transition: opacity 300ms linear;
  transition: opacity 300ms linear; }

.fade-enter {
  opacity: 0;
  z-index: 1; }

.fade-enter.fade-enter-active {
  opacity: 1;
  -webkit-transition: opacity 300ms linear;
  transition: opacity 300ms linear; }

/* exit */
.fade-exit {
  opacity: 1; }

.fade-exit.fade-exit-active {
  opacity: 0;
  -webkit-transition: opacity 5000ms linear;
  transition: opacity 5000ms linear; }

.fade-exit-done {
  opacity: 0; }

.loading-overlay {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 300px;
  background: white; }

.loading-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 120px;
  font-size: 20px;
  font-weight: bold;
  height: 85px;
  padding: 10px;
  color: #bababa; }

html, body {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  height: 100%;
  font-family: 'Open Sans', sans-serif; }

/* https://css-tricks.com/inheriting-box-sizing-probably-slightly-better-best-practice/ */
*, *:before, *:after {
  box-sizing: inherit; }

ul, li {
  padding: 0;
  margin: 0; }

textarea:focus, input:focus {
  outline: none; }

*:focus {
  outline: none; }

.box-shadow-light {
  box-shadow: 0 1px 3px rgba(128, 128, 128, 0.2); }

.box-shadow-heavy {
  box-shadow: 0 5px 15px rgba(128, 128, 128, 0.5); }

#app {
  height: 100%; }

#wrapper {
  display: grid;
  height: 100%;
  grid-template-columns: auto;
  grid-template-areas: "main"; }

main {
  position: relative;
  grid-area: main;
  display: flex;
  justify-content: center;
  background-color: white;
  padding: 20px; }

main > div {
  width: 100%; }

.main-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100px;
  width: 100%;
  margin-bottom: 30px; }

.main-header h1 {
  float: left;
  color: #3d3d3d;
  margin-left: 20px; }

.main-header .create-new-btn {
  float: right;
  width: 150px;
  margin-right: 20px;
  padding: 5px;
  border: thin solid black;
  cursor: pointer; }

@media screen and (min-width: 700px) {
  #wrapper {
    display: grid;
    height: 100%;
    grid-template-columns: 50px auto;
    grid-template-areas: "leftnav main"; } }

@media screen and (min-width: 1000px) {
  #wrapper {
    grid-template-columns: 150px auto; }
  main > div {
    width: 1000px; }
  .tasks-main {
    width: 100%; } }

@media screen and (min-with: 1200px) {
  main > div {
    width: 1200px; }
  .tasks-main {
    width: 100%; } }

